<template>
    <div id="stayls">
        <headerclass></headerclass>
        <pu-blicto></pu-blicto>
        <div class="Whole">
            <div class="claope">
                <div class="management">
                    <p class="mentero">一键下载的查询结果，如果是多个文件则为压缩包形式，需解压后，使用加解密工具解密。一键回传时，可回传单个加密文件，也可把加密文件压缩后回传（建议打包后回传）。</p>
                </div>
                <div class="identification">
                    <p  style="float: left;font-size: 18px;font-weight: 550">查询管理</p>
                    <div class="queryMent">
                        <label for="" @click="informweo">查询</label>
                        <span class="tioner">查询结果：</span>
                        <p @click="downloadwell" class="doewnlo">一键下载</p>
                        <a href="javascript:;" class="Onecki" style="position: relative">
                            <span class="ketle">一键回传</span>
                            <input type="file"   ref="inputer" @change="fileUpload" class="flieing" v-if="clearShow"/>
                        </a>
                    </div>
                    <div class="scenario">
                        <el-select v-model="scene" placeholder="请选择应用场景">
                            <el-option
                                    v-for="item in option"
                                    :key="item.appId"
                                    :label="item.sceneName"
                                    :value="item.appId">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="palerif">
                        <el-select v-model="value" placeholder="请选择客户名称">
                            <el-option
                                    v-for="item in options"
                                    :key="item.custId"
                                    :label="item.custName"
                                    :value="item.custId">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="">
                    <el-table
                            :data="tableData"
                            style="width: 100%">
                        <el-table-column
                                label="序号"
                                width="70"
                                type="index">
                        </el-table-column>
                        <el-table-column
                                prop="requestName"
                                label="请求名称"
                                >
                        </el-table-column>
                        <el-table-column
                                prop="requestDate"
                                label="请求时间">
                        </el-table-column>
                        <el-table-column
                                prop="custName"
                                label="客户名称">
                        </el-table-column>
                        <el-table-column
                                prop="custNumber"
                                label="客户编号">
                            <template slot-scope="scope">
                                    <el-button @click="handleEdit(scope.$index, scope.row)">
                                        {{scope.row.custNumber}}
                                    </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="number"
                                label="查询信息">
                            <template slot-scope="scope">
                                <el-button
                                        @click="handleDelete(scope.$index, scope.row)">下载
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                prop="isReturn"
                                label="结果回传">
                            <template slot-scope="scope" >
                                <el-button @click="balanceDelete(scope.$index, scope.row)" v-if="scope.row.isReturn === 0">回传</el-button>
                                <el-button @click="balanceDelete(scope.$index, scope.row)" class="buern" v-if="scope.row.isReturn === 1">已回传</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="paging">
                    <div class="block_plo">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :prev-text="'上一页'"
                                :next-text="'下一页'"
                                :page-count="pageCount"
                                :page-size="pagesize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pageTotal">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
<!--        回传-->
        <div class="Modal">
            <div class="modelto secret" v-if="secret">
                <div class='list-wll'>
                    <div class="Adtiko">
                        <p>回传</p>
                    </div>
                    <div class="sronly-xs">
                        <div class="tece_le">
                            <label for="">选择文件:</label>
                            <span class="kioser" v-if="file">{{file.name}}</span>
                            <a href="javascript:;" class="fellop">
                                <span class="ketle">上传</span>
                                <input type="file" name="file"  ref="info" @change="changeImage($event)" class="flieing" id="file"/>
                            </a>
                        </div>
                        <div class="form-group plert" style="margin-top: 30px;">
                            <button
                                    type="primary"
                                    @click="Submission"
                                    v-loading.fullscreen.lock="fullscreenLoading" class="calopi">
                                提交
                            </button>
                            <button class="Cancel" @click="calkoe">取消</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import headerclass from "@views/Charging/header/header";
    import puBlicto from "@views/Charging/Administration/puBlicto";
    import {queryInfoList,uploadResult,downQueryInfo,getAllCu,getAllAppScena,downNotReturnInfo,batchResult} from "@/network/admin";
    import {config} from "@/common/config";

    export default {
        name: "stayls",
        data() {
            return {
                tableData: [{
                }],
                selectedItem:{},
                loading: true,
                currentPage: 1,
                pagesize: 10,
                pageTotal: 0,
                pageCount: 0,
                fileList: [],
                searchFile: "",
                secret:false,
                custId:'',
                numer:'',
                information:'',
                fullscreenLoading: false,
                file:'',
                //客户名称
                options: [],
                value:'',
                option: [],
                scene:'',
                clearShow:true//文件上传
            }
        },
        created: function () {
            this.hadleGetFilesListApi();
            this.Customer();
            this.Scenelist()
        },
        activated:function(){
            this.hadleGetFilesListApi();
            this.Customer();
            this.Scenelist()
        },
        components: {
            headerclass,
            puBlicto,
        },
        methods: {
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.hadleGetFilesListApi();
                var itemsPerPage = this.pagesize;
            },
            // 控制页面的切换
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
                var currentPage = this.currentPage;
                this.hadleGetFilesListApi();
            },

            hadleGetFilesListApi() {
                var data = {
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize,
                    custId:this.value,
                    appId:this.scene
                };
                queryInfoList(data)
                    .then(res => {
                        // console.log(res.data.infos[0].custId);
                        this.tableData = res.data.infos;
                        this.pageTotal = res.data.total;
                        this.pageCount = res.data.pages;
                    })
                    .catch({});
            },
            handleEdit(index, row) {
                let routeData = this.$router.resolve({
                    path: "/Charging/Administration/administration",
                });
                window.open(routeData.href, "_blank");
            },
            handleDelete(index, row) {
                this.logId = row.logId;
                this.information = Object.assign({}, row);
                var formData = new FormData();
                let logId = this.information.logId;
                formData.append('logId', logId);
                downQueryInfo(formData).then(res => {
                    var logId = this.logId;
                    if (res.code) {
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                        return false
                    }
                    if (res.code == -300) {
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                        return false
                    }
                    window.location.href = config().url + '/customer/downQueryInfo?logId='+logId;
                })

            },
            //回传打开获取编号
            balanceDelete(index, row) {
                this.secret= true;
                this.logId = row.logId;
                this.selectedItem = Object.assign({}, row);
            },
            changeImage(e){
                this.file = e.target.files[0];
            },
        //    回传提交
            Submission(){
                var formData = new FormData();
                formData.append('file', this.file);
                if(!this.file){
                    layer.msg("请选择文件", {
                        icon: 2,
                        time: 1500
                    });
                    return false
                }
                let logId = this.selectedItem.logId;
                formData.append('logId', logId);
                this.fullscreenLoading = true;
                uploadResult(formData).then(res => {
                    if (res.code== 0) {
                        this.fullscreenLoading = false;
                        layer.msg(res.msg, {
                            icon: 1,
                            time: 1500
                        });
                        this.secret= false;
                        this.hadleGetFilesListApi();
                        this.file ='';
                    } else if(res.code){
                        this.fullscreenLoading = false;
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                    }
                })

            },
        //  回传隐藏
            calkoe(){
                this.secret= false;
                this.file ='';
            },
            //  回传隐藏
            calkoe(){
                this.secret= false;
            },
        //   获取所有客户名称
            Customer(){
                getAllCu().then(res => {
                    this.options = res.data;
                    this.options.unshift({
                        custName: "请选择客户名称",
                        custId: ''
                    })
                })
            },
        //获取所有应用场景列表
            Scenelist(){
                getAllAppScena().then(res => {
                    this.option = res.data;
                    this.option.unshift({
                        sceneName: "请选择应用场景",
                        appId: ''
                    })
                })
            },
        // 下载
            downloadwell(){
                 let custId = this.value;
                let appId = this.scene;
                const data = {
                    custId: custId,
                    appId: appId
                };
                downNotReturnInfo(data).then(res => {
                    if (res.code) {
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                        return false
                    }
                    if (res.code === -322) {
                        layer.msg(res.msg, {
                            icon: 2,
                            time: 1500
                        });
                        return false
                    }
                    window.location.href = config().url + '/customer/downNotReturnInfo?appId='+ appId + '&custId='+custId;
                })
            },
            fileUpload(e){
                this.file = e.target.files[0];
                if(this.file == undefined){

                } else{

                    var formData = new FormData();
                    formData.append('file', this.file);
                    this.clearShow = false;
                    batchResult(formData).then(res => {
                        if (res.code== 0) {
                            layer.msg(res.msg, {
                                icon: 1,
                                time: 1500
                            });
                            this.file = '';
                            this.clearShow = true;
                        } else if(res.code){
                            layer.msg(res.msg, {
                                icon: 2,
                                time: 1500
                            });
                            this.clearShow = true;
                        }
                    })
                }
            },
            //查询信息
            informweo(){
                var data = {
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize,
                    custId:this.value,
                    appId:this.scene
                };
                queryInfoList(data).then(res => {
                	if(res.data.infos.length == 0){
                        $('.Onecki').css({"pointer-events":"none","cursor":"default","color":'#999c9c',"border":"1px solid #999c9c"});
                        $('.doewnlo').css({"pointer-events":"none","cursor":"default","color":'#999c9c',"border":"1px solid #999c9c"});
                    } else{
                        $('.Onecki').css({"pointer-events":"auto","cursor":"pointer","color":'#4ad7b7',"border":"1px solid #4ad7b7"});
                        $('.doewnlo').css({"pointer-events":"auto","cursor":"pointer","color":'#4ad7b7',"border":"1px solid #4ad7b7"});
                    }
                    this.tableData = res.data.infos;
                    this.pageTotal = res.data.total;
                    this.pageCount = res.data.pages;
                    this.hadleGetFilesListApi();
                })
            }
        }
    }
</script>

<style>
    @import "~@assets/css/user/admintisop.css";
    .buern{
        pointer-events: none;
        color: #ccc;
        disabled:disabled;
    }
</style>